import React, { Component } from "react";
import YouTube from 'react-youtube';
import '../App.css';

export default class About extends Component {
    render() {

        //Video
        //https://www.youtube.com/watch?v=Lev9s2FDBxo
        const vidID = "Lev9s2FDBxo";
        const opts = {
            height: '250',
            width: '390',
          };

        return(
            <div className="">
                <h3>Hello,</h3>
                <p className="">my name is Zoe Kniskern and welcome to my personal website! Take a look at some of my work or read below to find out a little more about me:</p>
                <h5>On the grind...</h5>
                <p className="">I am a 5th year student studying New Media Interactive Development at the Rochester Institute of Technology, but what else? I began college as an Industrial Designer, and still maintain a huge respect and admiration for the field. After attending FITC Toronto in the Spring of 2015 and 2016 and hearing from speakers such as Jared Ficklin, I connected with the technical side of interactive art. Combining logical and creative solutioning in programming is a satsifying and fruitful challenge.</p>
                <p className="">I chose an immersion (additional coursework paths at RIT) in digital literature because the intersection of narrative and digital media guides the work in fields such as interactive installation, exhibit design, and generative art; fields that I am interested in working in. Early hypertext and text based computer games began a partnership and I enjoy studying how literature can be adapted to a technology saturated world.</p>
                <br/>
                <h5>Looking to the future...</h5>
                <p className="">I have worked in both agile and waterfall workflows during my co-ops have seen the difficulty in balancing the interests of designers, developers and business needs. My degree focuses on technical skills, but I am also interested in management. Managers who respect the importance of all these intersecting fields can enable skilled individuals to focus on their passions rather than be impeded by process and logistics.</p>
                <p className="">Interactive experiences and museum exhibit design, scene at platforms such as the SXSW Interactive conference and science museums are areas that I would also love to work in. Interactive Development is often an ambiguously defined field and I enjoy that. The potential to work on projects in AR, VR, that combine physical and digital interfaces, that appeal to a wide variety of audiences or that push the experimental limits and more is exciting to me.</p>
                <br/>
                <h5>More than just a degree...</h5>
                <p className="">Throughout my college career (and before that) I have worked hard to maintain personal hobbies in addition to my schoolwork. I am a lover of musical theater, drawing and Latin Dance, to mention a few.</p>
                <p className="">Some of the accomplishments that I'm proud of include: being a speaker at the TEDxSpringfield conference in December 2014, making it into the top 6 couples at the 2017 Boston Brazilian Dance Festival Jack and Jill, and most recently being accepted as a participant of the Transnational Workshop collaboration between the University of Paris 8 and RIT to create interactive installations that will be displayed at Monparnasse in Jan 2019.</p>
                <YouTube videoId={vidID} opts={opts}/>
                <p className="">If you want to know more about any of these adventures (or some that I haven't mentioned), have a book recommendation, or just want to say hello, feel free to email me at zxk3766@rit.edu to get in touch!</p>
            </div>
        )
    }
}